.navigate__link {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    cursor: pointer;
    margin: 0;
    text-decoration: none;
    color: #fff;
    transition: opacity 0.3s ease-in;
}

.navigate__link:hover {
    opacity: 0.6;
}